.worksItem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  margin-bottom: 70px;
  margin-left: 10px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  border-radius: 3p;
  background: cover;
  &:nth-child(1n+7){
   margin-bottom: none;
  }
  @media screen and(max-width: 991px) {
    width: 80%;
    height: 600px;
    margin: 0;
    margin-bottom: 100px;
  }
  @media screen and(max-width: 768px){
    width: 98%;
  }
  .Image {
    width: 400px;
    height: 250px;
    opacity: 0.7;
    object-fit: cover;
    @media screen and(max-width: 991px) {
      width: 100%;
      height: 400px;
    }
  }
  .Description {
    color: #eee;
    font-family: Poppins;
    font-size: 14px;
    font-weight: 500;
    padding: 15px;
    letter-spacing: 0.5px;
    background-color:grey;
    .More{
      font-size: 16px;
      color: blue;
      text-decoration: underline;
    }
    @media screen and(max-width: 991px) {
      margin-top: 10px;
    }
  }
  .ItemOverlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 250px;
    background: transparent;
    z-index: 2;
  @media screen and(max-width: 991px){
height: 400px;
  }
    .Follow {
      width: 100%;
      height: 250px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .Web {
        color: #eb83f8;
        font-size: 16px;
        position: relative;
        margin-bottom: 10px;
        transform: translateY(-15px);
        transition: all 0.6s;
        opacity: 0;
        z-index: 5;
      }
      &:hover .Web {
        transform: translateY(0);
        opacity: 1;
      }
      .Git {
        color: #eb83f8;
        font-size: 18px;
        position: relative;
        -webkit-transform: translateY(15px);
        transform: translateY(15px);
        transition: all 0.6s;
        opacity: 0;
        z-index: 5;
      }
    }
    &:before {
      content: "";
      position: absolute;
      width: 0;
      height: 50%;
      background: #fff;
      transition: all 0.4s;
      left: 0;
      top: 0;
    }
    &::after {
      content: "";
      position: absolute;
      width: 0;
      height: 50%;
      background: #fff;
      transition: all 0.4s;
      right: 0;
      bottom: 0;
      align-self: auto;
    }
    &:hover:before {
      background:rgba(37, 17, 112, .7);
      width: 100%;
    }
    &:hover:after {
      background:  rgba(37, 17, 112, .7);
      width: 100%;
    }
    &:hover .Follow .Git {
      transform: translateY(0);
      opacity: 1;
    }
    &:hover .Follow .Web {
      transform: translateY(0);
      opacity: 1;
    }
  }
  .ButtonsWorks {
    display: flex;
    width: 300px;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1234;
  }
}
 .Description.active{
   position: absolute;
   z-index: 999999;
 }