.WorksParallax {
  composes: Parallax from "../skills/skills.module.scss";
}
.worksContainer {
  composes: container from "../../index.css";
  padding-top: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;

  .WorksList {
    min-width: 100%;
    display: flex;
    justify-content: space-between;
    padding-right: 50px;
    flex-wrap: wrap;
    overflow-y: scroll;
    max-height: 700px;
    @media screen and(max-width: 1200px) {
      padding: 0 30px;
      &::-webkit-scrollbar {
        display: none;
      }
    }
     @media screen and(max-width: 991px) {
       justify-content: center;
     }
       @media screen and(max-width: 768px){
    padding: 0;
  }
    &::-webkit-scrollbar {
      width: 10px;
      background-color: rgba(37, 17, 112, 0.9);
    }
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      background-color: transparent;
      border-radius: 10px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #eb83f8;
      border-radius: 10px;
      background-image: -webkit-linear-gradient(
        0deg,
        rgba(255, 255, 255, 0.5) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.5) 50%,
        rgba(255, 255, 255, 0.5) 75%,
        transparent 75%,
        transparent
      );
    }
  }
}
